.footer_container {
 background-color: #242424;
 padding: 4rem 0 2rem 0;
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;
}

.footer_subscription {
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;
 text-align: center;
 margin-bottom: 24px;
 padding: 24px;
 color: #fff;
}

.footer_subscription > p {
 font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
  "Lucida Sans", Arial, sans-serif;
}

.footer_text {
 margin-bottom: 24px;
 font-size: 20px;
}

.footer_input {
 padding: 8px 20px;
 border-radius: 5px;
 margin-right: 10px;
 outline: none;
 border: none;
 font-size: 16px;
 margin-bottom: 16px;
 border: 1px solid #fff;
}

.links {
 width: 100%;
 max-width: 1000px;
 display: flex;
 justify-content: center;
}

.links_wrapper {
 display: flex;
}

.links_items {
 display: flex;
 flex-direction: column;
 align-items: flex-start;
 margin: 16px;
 text-align: left;
 width: 160px;
 box-sizing: border-box;
}

.links_items h2 {
 margin-bottom: 16px;
 color: #fff;
}

.links_items a {
 color: #fff;
 text-decoration: none;
 margin-bottom: 8px;
}

.links_items a:hover {
 color: #b9b3b3;
 transition: 0.2s ease-out;
}

.footer_input h2 {
 margin-bottom: 32px;
}

.footer_input::placeholder {
 color: #b1b1b1;
}


                     /* social icons */

.social_icon_link{
    color: #fff;
    font-size: 24px;
}

.social_media{
    max-width: 1000px;
    width: 100%;
    }

.social_wrap{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    max-width: 1000px;
    margin: 40px auto 0 auto;
}

.social_icons{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 240px;
}

.social_logo{
        color: #fff;
        justify-self: start;
        margin-left: 20px;
        text-decoration: none;
        font-size: 2rem;
        display: flex;
        align-items: center;
        margin-bottom: 10px;
}

.website_rights{
    color:#fff;
    margin-bottom: 0px;
}

@media screen and (max-width: 820px){
    .links{
 padding: 2rem;
    }

    .footer_input{
        width: 100%;
    }
    
    .btn{
        width: 100%;
    }

    .links_wrapper{
        flex-direction: column;
    }

    .social_wrap{
        flex-direction: column;
    }
}


@media screen and (max-width: 530px){
    .links{
        padding: 1rem;
           }

           
    .footer_input{
        width: 70%;
    }
    
    .btn{
        width: 70%;
    }

    .links_wrapper{
        margin: -30px;
    }
}