.cards {
 padding: 4rem;
 background: #fff;
}

h1 {
 text-align: center;
}

.cards_container {
 display: flex;
 flex-flow: column;
 align-items: center;
 max-width: 1120px;
 width: 90%;
}

.cards_wrapper {
 position: relative;
 margin: 50px 0 45px;
}

.cards_items {
 margin: 24px;
}

.cards_items {
 display: flex;
 flex: 1;
 margin: 0 1rem;
 border-radius: 10px;
}

.cards_item_link {
 display: flex;
 flex-flow: column;
 width: 100%;
 box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
 -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
 filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
 border-radius: 10px;
 overflow: hidden;
 text-decoration: none;
}

.cardsItem_picwrap {
 position: relative;
 width: 100%;
 padding-top: 67%;
 overflow: hidden;
}

.fade-img {
 animation-name: fade-img;
 animation-duration: 2s;
}

.cardsItem_picwrap::after {
 content: attr(data-category);
 position: absolute;
 bottom: 0;
 margin-left: 10px;
 padding: 6px 8px;
 max-width: calc((100%) - 60px);
 font-size: 12px;
 font-weight: 700;
 color: #fff;
 background-color: #1f98f4;
 box-sizing: border-box;
}

.cardsItem_img {
 position: absolute;
 top: 0;
 right: 0;
 bottom: 0;
 left: 0;
 display: block;
 width: 100%;
 height: 100%;
 max-width: 100%;
 max-height: 100%;
 object-fit: cover;
 transition: all 0.5s linear;
}

.cardsItem_img:hover {
 transform: scale(1.1);
}

.cardsItem_info {
 padding: 20px 30px 30px;
}

.cardsItem_text {
 color: #252e48;
 font-size: 18px;
 line-height: 24px;
}

@media only screen and (min-width: 1200px) {
 .content__blog__container {
  width: 84%;
 }
}

@media only screen and (min-width: 1024px) {
 .cards_items {
  display: flex;
 }
}

@media only screen and (max-width: 1024px) {
 .cards_items {
  margin-bottom: 2rem;
  display: block;
 }
}

@media only screen and (max-width: 500px) {
 .cardsItem_text {
  color: #252e48;
  font-size: 15px;
  line-height: 24px;
 }
}
