video{
    object-fit:cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
}

.hero_container{
    /* background: url('/src/images/img-home.jpg') center center/cover no-repeat; */
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit:contain;
}

.hero_container > h1 {
    color: #fff;
    font-size: 100px;
    margin-top: -100px;
}

.hero_container > p{
    margin-top: 8px;
    color: #fff;
    font-size: 32px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;

}

.hero_btns{
    margin-top:32px;
    
}

.hero_btns .btn{
    margin: 6px;
    
}

.hero_btns .btn:hover{
    color: white;
    background-color: transparent;
}

.fa-play-circle{
    margin-left: 4px;
}

@media screen and (max-width: 960px){
    .hero_container > h1{
        font-size: 70px;
        margin-top: -150px;
    }
}

@media screen and (max-width: 768px){
    .hero_container > h1{
        font-size: 50px;
        margin-top: -100px;
        display: flex;
        align-items: center
    }

    .hero_container > p{
        font-size: 30px;
    }

    .btn-mobile{
        display: block;
        text-decoration: none;
    }

    .btn{
        width: 100%;
    }
}



@media screen and (max-width: 420px){
    .hero_container > h1{
        font-size: 40px;
        margin-top: -80px;
       
    }

    
    .hero_container > p{
        font-size: 25px;
    }

}


@media screen and (max-width: 335px){
    .hero_container > h1{
        font-size: 35px;
        margin-top: -80px;
       
    }

    
    .hero_container > p{
        font-size: 20px;
    }

}